import { combineReducers } from 'redux'


/// App reducer
function saveScrollPosition(state, action) {

  /// Update an app to store its scrollPos.
  return {
    ...state,
    appsList: {
      ...state.appsList,
      [action.payload.currentApp]: {
        ...state.appsList[action.payload.currentApp],scrollPos: action.payload.scrollPos
      }
    }
  }
}
function updateAppOnDataReceived(state, action, currentApp){

	/// Update an app to have the data it received from a fetch.
  return {
    ...state,
    appsList: {
      ...state.appsList,
      [currentApp]: {
        ...state.appsList[currentApp], hasData: action.payload
      }
    }
  }

}
const updateAppSearchTerm = (state, action, currentApp) => {
  return {
    ...state,
    appsList: {
      ...state.appsList,
      [currentApp]: {
        ...state.appsList[currentApp], searchTerm: action.payload,  hasData: false
      }
    }
  }

}

const initialAppState = {
    currentApp: 'Chilli Hot',
		apps:['Chilli Hot','reddit',  'Hacker News', 'Tester', 'Contact', 'Shopping'],
    current: 0,
    appsList:{
			"Chilli Hot": {
				color:'red',
				navItems:['n1', 'n2',  {icon:'menu'}, {icon:'shopping_cart'}, {icon:'search'}],
				hasData:false,
				baseURL: false,
				searchTerm: null
				},
			"reddit": {color:'blue', navItems:[{icon:'menu'}, {icon:'search'}], hasData:false, baseURL: 'https://www.reddit.com/r/', searchTerm: null},
			"Hacker News": {color:'green', navItems:[{icon:'menu'}, {icon:'search'}], hasData:false, baseURL: 'https://hn.algolia.com/api/v1/search?query=', searchTerm: null},
			"Shopping": {color:'yellow', navItems:[{icon:'menu'}, {icon:'shopping_cart'}], hasData:false, baseURL: false, searchTerm: null},
			"Contact": {color:'grey', navItems:[ {icon:'menu'},{icon:'email'}, {icon:'phone'} ], hasData:false, baseURL: false, searchTerm: null},
			"Tester": {color:'black', navItems:[ {icon:'menu'} ], hasData:false, baseURL: false, searchTerm: null},
		}
};
const appReducer = (state = initialAppState, action) => {
    switch (action.type) {
			case 'CHANGE_APP_STATE':
				const nextAppIndex = (state.current === state.apps.length - 1) ? 0 : state.current + 1
				const nextApp = (action.payload) ? action.payload : state.apps[nextAppIndex]
				return {
					...state,
					currentApp: nextApp,
					current: nextAppIndex
				};
			case 'SAVE_SCROLL_POS':
				return saveScrollPosition(state, action)
			case 'APP_GOT_DATA':
				return updateAppOnDataReceived(state, action, state.currentApp)
			case 'SET_SEARCH_TERM':
				return updateAppSearchTerm(state, action, state.currentApp)
			default:
				return state;
    }
};

/// SearchbarComponent reducer
const initialSearchState = {opened:false, currentSearchTerm:''}
const searchReducer = (state = initialSearchState, action) => {
	switch(action.type){
		case 'TOGGLE_SEARCHBAR':
			const opened = !state.opened
			return {opened, currentSearchTerm:state.currentSearchTerm }
		case 'CLOSE_SEARCHBAR':
			return {...state, opened:false}
		case 'OPEN_SEARCHBAR':
			return {...state, opened:true}
		case 'SEARCH_TERM_SELECTED':
		   return {opened:state.opened, currentSearchTerm: action.payload }
		default:
			return state
	}
}

/// Fetch reducer
const initialFetchState = { isFetching: false, data: [], error: false };
const fetchReducer = (state = initialFetchState, action) => {
	switch(action.type){

		/// Data has been requested, waiting
		case 'DATA_REQUEST_IN_PROGRESS':
      return { ...state, isFetching: true };

		/// Data has been retrieved (up to caller to destructure received data (<< not accurate))
		case 'DATA_RECEIVED':
      return { ...state, isFetching: false, data: action.payload, error: false };

		/// Data request has completed with error/failure
		case 'DATA_REQUEST_ERROR':
			 return {...state, isFetching: false, error: action.payload}

		default:
			 return state;
	}
}



const rootReducer = combineReducers({
	//auth: authReducer,
	//project: projectReducer,
	//nav: navReducer,
	app: appReducer,
	//app2: app2Reducer,
	searchbar: searchReducer,
	fetch: fetchReducer
	//cartOpen:cartReducer
});

export default rootReducer
