import React/*, { useEffect }*/ from 'react';
import { useSelector, useDispatch/*, useStore*/ } from 'react-redux';
import { myFetcher } from './fetchAction';

const ChilliHotComponent = () => {

const App = useSelector(state => state);
console.log(App)




	/// Variables
	const URL = 'http://localhost:2000/itemsData.php'
	const currentApp = useSelector(state => state.app.currentApp);
	const dispatch = useDispatch();
	const hasData = useSelector(state => state.app.appsList[currentApp].hasData);

	if(!hasData){
		//console.log('no data - must fetch:', hasData);
		myFetcher(URL, dispatch)
	} else {
		console.log('have data ok:', hasData);
	}


	/// Prepare component content.
	let itemsList = [];
	if(hasData && hasData !== 'Error'){
		itemsList = hasData.items.map((item, i) => {
		//itemsList = hasData.hits.map((item, i) => {
			return (<div key={i} className='gallery-box'>{item.fields.title}</div>)
			//return (<div key={i} className='gallery-box'>
			//{item.title}<br />
			//{item.created_at} <br />
			//{item.author} <br />
			//<a href={item.url}>{item.url}</a><br />
			//</div>)
		});
	} else if(hasData && hasData === 'Error'){
		/** in here - load dummy data? */
		itemsList = [ (<div key='0'>There was an error getting data.</div>) ]
	} else {
		itemsList = [ (<div key='0'>Loading... Please wait</div>) ]
	}


	/// Return component.
	const className = (hasData && hasData === 'Error') ? '' : 'container-center' ;
	return (<div id='chilli' className={className}>
			{ itemsList }
		</div>)
}

export default ChilliHotComponent
