import React from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { myFetcher } from './fetchAction';

const HackerNewsComponent = () => {

	/// Variables
	const dispatch = useDispatch();
	const currentApp = useSelector(state => state.app.currentApp);
	const hasData = useSelector(state => state.app.appsList[currentApp].hasData);

	const baseURL = useSelector(state => state.app.appsList[currentApp].baseURL);
	const searchTerm = useSelector(state => state.app.appsList[currentApp].searchTerm);

	if(!searchTerm){

		const openSearchBar = () => {
			dispatch({ type: 'TOGGLE_SEARCHBAR' });
		}

		return (<div id='hackerApi' className=''>
							Please choose a topic to search for <button onClick={openSearchBar}>Choose a Topic</button>
					</div>)
	}

	const isOnline = (!navigator.onLine) ? false : true;
	const URL = (isOnline) ? `${baseURL}${searchTerm}` : 'http://localhost:2000/HackerNewsApi.php'

	if(!hasData){
		myFetcher(URL, dispatch)
	}


	/// Prepare component content.
	let itemsList = [];
	if(hasData && hasData !== 'Error'){

		/// Sort items by date.
		const sortedItems =  hasData.hits.sort((a, b) => parseInt(b.created_at) - parseInt(a.created_at));

		itemsList = sortedItems.map((item, i) => {

			/// Format the date
			const d = new Date(item.created_at)
			const dtf = new Intl.DateTimeFormat('en', { year: 'numeric', month: 'short', day: '2-digit' })
			const [{ value: mo },,{ value: da },,{ value: ye }] = dtf.formatToParts(d)
			const formattedDate = `${ye}-${mo}-${da}`

			/// Return an array item with the appropriate content.
			return (<div key={i} className='hackerApi-box'>
				<div className='title'>{item.title}</div>
				<div className='date'>{formattedDate}</div>
				<div className='author'>Author: {item.author}</div>
				<a href={item.url}>{item.url}</a><br />
				</div>)
		});

	} else if(hasData && hasData === 'Error'){
		/** in here - load dummy data? */
		itemsList = [ (<div key='0'>There was an error getting data.</div>) ]
	} else {
		itemsList = [ (<div key='0'>Loading... Please wait</div>) ]
	}


	/// Return component.
	const className = (hasData && hasData === 'Error') ? '' : 'container-centerX' ;
	return (<div id='hackerApi' className={className}>
			{ itemsList }
		</div>)
}

export default HackerNewsComponent
