import React/*, { useEffect }*/ from 'react';
import { BrowserRouter, Switch, Route } from 'react-router-dom';
import Navbar from "./Navbar";
import Display from "./Display";
import Footer from "./Footer";

function App() {
	
  return (
    <BrowserRouter>
      <div className="App">
        <Navbar />
        <Switch>
          <Route exact path='/' component={Display} />
        </Switch> 
        <Footer />
      </div>
    </BrowserRouter>
  );
}

export default App;
