import React from 'react';
import { useSelector } from 'react-redux';
import ChilliHotComponent from './ChilliHotComponent';
import HackerNewsComponent from './HackerNewsComponent';
import RedditComponent from './RedditComponent';

const Display = () => {

	const currentApp = useSelector(state => state.app.currentApp);

	if(currentApp === 'reddit'){
		return (<div id='Display' className='container'>
				<RedditComponent />
			</div>)
	}

	if(currentApp === 'Hacker News'){
		return (<div id='Display' className='container'>
				<HackerNewsComponent />
			</div>)
	}

	if(currentApp === 'Chilli Hot'){
		return (<div id='Display' className='container'>
				<ChilliHotComponent />
			</div>)
	}

	if(currentApp === 'Tester'){

		/// Click action on buttons for fonts sizes
		const handlePlus = (e) => {
			console.log(e.target.innerHTML, window.innerWidth, window.innerHeight);
			const el = e.target.innerHTML

			const w = window.innerWidth
			const h = window.innerHeight
			let f = parseInt(window.getComputedStyle(document.querySelector('.brand-logo')).fontSize);
			let i = parseInt(window.getComputedStyle(document.querySelector('nav i')).fontSize);
			f = parseInt(f)
			f = (el === 'increase font size') ? parseInt(f + 2) : parseInt(f - 2)
			i = (el === 'increase font size') ? parseInt(i + 1) : parseInt(i - 1)
			document.querySelector('.brand-logo').style.fontSize = f + 'px'
			document.querySelector('nav i').style.fontSize = i + 'px'

			document.querySelector('.readout').innerHTML = `Screen Width(px): ${w}<br /> Screen Height(px): ${h}<br /> Logo font(px): ${f}<br /> Icon font(px): ${i}<br />`;
		}

		return (
			<div id='Display' className='container'>
				<div id='tester'>
					<button  onClick={handlePlus}>increase font size</button><br />
					<button  onClick={handlePlus}>decrease font size</button><br />
					<div className='readout'></div>

				</div>
			</div>
		)
	}


	/// Return default (empty) component.
	return (<div id='Display' className='container'>   </div>)
}

export default Display


